* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.value {
  text-align: center;
  letter-spacing: 4px;
  text-shadow: 2px 2px 3px #000;
  color: #14a3dc;
  align-items: center;
  font-size: 35px;
  font-weight: 600;
}

.timer {
  text-transform: uppercase;
  align-items: center;
  display: flex;
}

.field {
  flex-direction: column;
  margin-right: 25px;
  display: flex;
}

.label {
  text-align: center;
  font-size: 14px;
  font-weight: 700px;
  text-shadow: 0 4px 4px #c4b59d, 0 -2px 1px #fff;
}

